import React, { createRef } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as ui_actions from 'src/redux/actions/ui'
import * as word_actions from 'src/redux/actions/word'
import * as domain_actions from 'src/redux/actions/domain'
import { Button, Input } from 'antd'
import { Styled } from './Search.styled'
import SelectTld from 'src/components/Search/SelectTld'
import WordPoss from '../WordPoss'
import PropTypes from 'prop-types'
import { navigate } from 'gatsby'
import {
  querystring_from_object,
  object_from_querystring,
} from 'universal-common-scripts/functions/urls'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/pro-solid-svg-icons'
import Recaptcha from 'react-google-invisible-recaptcha'

/*
 * WORD_FROM_URL = load word/tld from url querystring, setState, call redux action, etc
 */
const WORD_FROM_URL = true
/*
 * in production, use captcha
 * in development, will bypass it, call data directly
 */
const USE_CAPTCHA = process.env.GATSBY_ACTIVE_ENV !== 'development'

let tld_value = function(props) {
  return (
    Object.keys(props.tlds_user)[0] ||
    Object.keys(props.tlds_checked)[0] ||
    ''
  )
}

const parse_word_and_tld = function(user_input) {
  /*
   * prepare output
   */
  let word = user_input
  let tld = ''
  /*
   * process input
   */
  // if url contains ".", then assume what comes after is domain extension
  let doti = user_input.indexOf('.')
  if (doti !== -1) {
    // parse tld
    tld = user_input.substr(doti + 1).trim()
    // fix input
    word = user_input.substr(0, doti)
  }
  /*
   * output
   */
  return [word, tld]
}

/*
 * NOTES:
 * constructor() --- page loaded ... parse state from url
 * componentDidMount --- focus user input
 * 1) url changed ... will any other file be changing the url? if so, it will not contain any variables
 * 2) user changes word ... update state, change url
 * 3) user changes tld ... update state, change url
 */
class Search extends React.Component {
  constructor(props) {
    super(props)
    /*
     * parse values from url
     */
    let [word, tld] = this.read_location()
    /*
     * default values
     */
    this.state = {
      word: word || '',
      tld: tld || tld_value(props),
    }
    /*
     * dom manipulation
     */
    this.Input_ref = createRef()
  }

  async componentDidMount() {
    /*
     * on-page-load, focus user input
     */
    if (this.Input_ref.current) {
      this.Input_ref.current.focus()
    }
    /*
     * on page load, get data
     * if NOT using captcha, call validate_state directly
     * captcha will call it when it's loaded
     */
    if (!USE_CAPTCHA && WORD_FROM_URL) {
      this.validate_state()
    }
    /*
     * if no word in url, remove it from redux
     */
    if (!this.state.word) {
      this.props.word_actions.RX__word_input_str('')
    }
  }

  componentDidUpdate(prevProps) {
    /*
     * watch redux domains.tlds_user and domains.tlds_checked
     * on change, parse new tld
     */
    // let tld = tld_value(this.props)
    // if (tld !== this.state.tld) {
    //   // avoid duplicate calls -
    //   // set state immediately
    //   this.setState({ tld })
    //   // process new tld
    //   console.log('componentDidUpdate, update inputs')
    //   this.validate_state()
    // }
    /*
     * watch url for changes
     */
    if (this.props.location.search !== prevProps.location.search) {
      let [word] = this.read_location()
      if (word !== this.state.word) {
        this.input_word(word)
      }
    }
  }

  recaptcha_ready = () => {
    /*
     * if using captcha,
     * then this method will be called by captcha, when it's ready
     */
    if (USE_CAPTCHA && WORD_FROM_URL) {
      // get data
      this.validate_state()
    }
  }

  input_tld = (tld = '') => {
    // validate
    tld = tld.trim()
    if (tld === this.props.input_tld) return
    // avoid duplicate calls -
    // do not set state now, but AFTER Redux action,
    // in componentDidUpdate()
    this.props.domain_actions.RX__tld_add(tld, 'user')
    // set state
    this.setState(
      {
        tld,
      },
      // get data
      this.validate_state,
    )
  }

  input_word = (word = '') => {
    // validate
    word = word.trim()
    if (word === this.props.input_str) return
    // if url contains ".", parse TLD from input string
    let doti = word.indexOf('.')
    let tld = this.state.tld
    if (doti !== -1) {
      // parse tld
      tld = word.substr(doti + 1).trim()
      // fix input
      word = word.substr(0, doti)
    }
    // set state
    let newState = { word }
    if (tld && tld !== this.state.tld) {
      newState.tld = tld
    }
    this.setState(
      newState,
      // then,
      // validate/process inputs
      this.validate_state,
    )
  }

  validate_state = async () => {
    let word = this.state.word
    let tld = this.state.tld
    console.log('validate_state', word)

    /*
     * validate input
     */
    word = word.trim()
    tld = tld.trim()
    if (!word) return
    // already got data, this is duplicate call
    if (word === this.props.input_str && tld === this.props.input_tld) {
      return
    }

    /*
     * validate captcha
     * captcha.execute() will callback this.persist_state()
     */
    if (!USE_CAPTCHA) {
      // local server will ignore captcha_response,
      // so, bypass this step
      this.persist_state()
    } else {
      // remote server requires true captcha_response
      if (this.recaptcha && this.recaptcha.execute) {
        this.recaptcha.execute()
      }
    }
  }

  persist_state = async () => {
    let word = this.state.word
    let tld = this.state.tld
    console.log('persist_state', word)

    /*
     * validate captcha
     */
    let captcha_response = ''
    // local server not check captcha_response value
    if (USE_CAPTCHA) {
      captcha_response = this.recaptcha.getResponse()
      this.recaptcha.reset()
    }

    /*
     * set redux
     */
    if (this.props.input_str !== word || this.props.input_tld !== tld) {
      this.props.word_actions.RX__word_phrase(word, tld, { captcha_response, get_domains: this.props.domains })
    }

    /*
     * show in url + title
     */
    this.write_location(word, tld)
  }

  write_location = (word = '', tld = '') => {
    /*
     * set title
     */
    window.document.title = word // + (this.state.tld ? '.' + this.state.tld : '');
    /*
     * set url
     */
    if (WORD_FROM_URL) {
      // /{page_name}?word={word}&tld={tld}
      let obj = { word: word }
      if (tld) {
        obj.tld = tld
      }
      let search = querystring_from_object(obj)
      if (search !== this.props.location.search) {
        navigate(this.props.location.pathname + search)
      }
    } else {
      // /{page_name}
      navigate(this.props.location.pathname)
    }
  }
  read_location = () => {
    // usage:
    // let [word, tld] = this.read_location();
    let obj = object_from_querystring(this.props.location.search)
    let word = decodeURIComponent(obj.word || '').trim()
    let tld = decodeURIComponent(obj.tld || '').trim()
    return [word, tld]
  }

  render() {
    let {
      title,
      placeholder,
      cue,
      hideInput,
      domains,
      className,
      ...props
    } = this.props
    return (
      <Styled className={'Search ' + className}>
        <div className="content">
          <div className="wordart" data-wordart-src="" />

          {/*
           * Title
           * If mobile, show only last line (last item in array)!
           */}
          {!!title && <h1 className="title">{title}</h1>}

          {/*
           * Input Group
           */}
          <div className={'input-group'}>
            {!hideInput && (
              <Input
                ref={this.Input_ref}
                className="Input"
                placeholder={placeholder || 'enter a few words or a phrase...'}
                value={this.state.word}
                onChange={event => {
                  this.setState({
                    word: event.target.value,
                  })
                }}
                onKeyPress={event => {
                  if (event.key === 'Enter') {
                    this.input_word(this.state.word)
                  }
                }}
                onBlur={() => {
                  if (this.props.input_str && !this.state.word) {
                    this.setState({
                      word: this.props.input_str,
                    })
                  }
                }}
              />
            )}
            {!!domains && (
              <SelectTld
                value={this.state.tld}
                placeholder=""
                onSelect={value => {
                  this.input_tld(value)
                }}
              />
            )}
            <Button
              className="Button"
              onClick={() => {
                this.input_word(this.state.word)
              }}
            >
              <span className="hide-small">search&thinsp;&thinsp;</span>
              <FA
                icon={faSearch}
                className="faSearch"
                style={{ transform: 'scale(0.85)' }}
              />
            </Button>
          </div>

          {/*
           * Cue message
           */}
          {!!cue && (
            <div className="cue">
              <div className="p">{cue}</div>
            </div>
          )}

          {/*
           * Open POSS
           */}
          <div
            className="show_poss"
            onClick={() => {
              props.ui_actions.RX__toggle_poss()
            }}
          >
            <span>...</span>
          </div>
        </div>

        {/*
         * Recaptcha
         */}
        <Recaptcha
          ref={ref => this.recaptcha = ref}
          sitekey="6LeQt-MUAAAAAFHZGwmJnd58aStK2xF-6f8MBtm3"
          onResolved={this.persist_state}
          onLoaded={this.recaptcha_ready}
        />

        {/*
         * POSS content, below Search form
         */}
        <WordPoss {...props} />
      </Styled>
    )
  }
}

/*******************************************************************************************************
 * this.props DOCUMENTATION
 *******************************************************************************************************/

const mapDispatchToProps = dispatch => {
  return {
    domain_actions: bindActionCreators(domain_actions, dispatch),
    word_actions: bindActionCreators(word_actions, dispatch),
    ui_actions: bindActionCreators(ui_actions, dispatch),
  }
}

const mapStateToProps = function(state) {
  return {
    input_str: state.word.input_str, // string
    input_tld: state.word.input_tld, // string
    tlds_user: state.domain.tlds_user, // dict of tlds
    tlds_checked: state.domain.tlds_checked, // dict of tlds
  }
}

Search.propTypes = {
  domains: PropTypes.bool, // if true, this component will manage .tld in url (if false, will omit .tld from url)
  location: PropTypes.object,
}

export default connect(mapStateToProps, mapDispatchToProps)(Search)
