import { domain_suggestions } from 'src/redux/actions/api/v1'
import { message } from 'antd'

// const PRODUCTION = process.env.GATSBY_ACTIVE_ENV === 'production'

/*
 * input
 */
export function RX__word_input_str(str = '') {
  return {
    type: 'RX__word_input_str',
    listType: str,
  }
}

export function RX__word_input_tld(str = '') {
  return {
    type: 'RX__tld_input_tld',
    listType: str,
  }
}

/*
 * words
 */
export function RX__word_phrase(phrase = undefined, tld = '', config = { captcha_response: '', get_domains: false }) {
  return async (dispatch, getState) => {
    // default word
    let { word } = getState()
    if (phrase === undefined) {
      phrase = word.input_str
    }

    // input tld (set immediately if specified)
    if (tld) {
      dispatch({
        type: 'RX__word_input_tld',
        data: tld,
      })
    }

    // API options
    let options = {
      phrase: phrase,
      tld: tld,
      captcha_response: config.captcha_response,
    }
    if (config.get_domains) {
      options._domains = true
      options._tlds = true
      options._phrases = true
      options._wip_phrases = true
      options._wip_domains = true
    }

    // REQUEST
    let data = await dispatch(domain_suggestions(options)) || {}

    // if input tld not specified, set now, rather than before
    // this is to sync up the "default" on FE and BE
    if (!tld) {
      dispatch({
        type: 'RX__word_input_tld',
        data: data.tld,
      })
    }

    // update input_str AFTER got data, or not at all
    // because Search will check that <Input value /> = RX.word.input_str
    dispatch({
      type: 'RX__word_input_str',
      data: phrase,
    })

    // data
    dispatch({
      type: 'RX__tld_user',
      data: data.tld || tld,
    })

    dispatch({
      type: 'RX__word_chunks',
      data: data.phrase_chunks || [],
    })

    dispatch({
      type: 'RX__word_tld_chunk',
      data: data.phrase_tld || {},
    })

    dispatch({
      type: 'RX__domain_suggestions',
      results: {
        domains: data.domains || [],
        wip_domains: data.wip_domains || [],
        phrases: data.phrases || [],
        wip_phrases: data.wip_phrases || [],
      },
    })
    dispatch({
      type: 'RX__tlds_checked',
      data: data.tlds || [],
    })
  }
}